import { makeStyles, CardContent } from '@material-ui/core';
import { TOffer } from '../../../../types';
import MainCard from '../../../../ui-component/cards/MainCard';
import OffersTable from '../../offer/OffersTable';
import { useOrder } from '../../../../hooks';
import {
	stableSort,
	getComparator,
	getHeadCellById,
} from '../../../../utils/table';
import { headCells } from '../../offer/helper';

type Props = {
	offers: Array<TOffer>;
	isLoading: boolean;
	propertyId: string;
};

const useStyles = makeStyles({
	mainCard: {
		border: 'none',
	},
});

const OfferList = ({ offers, isLoading, propertyId }: Props) => {
	const classes = useStyles();
	const defaultOrder = getHeadCellById('CreatedAt', headCells);
	const { order, orderBy, handleRequestSort } = useOrder(defaultOrder);

	const sortedOffers = stableSort<TOffer>(offers, getComparator(order, orderBy));

	return (
		<MainCard title='' content={false} className={classes.mainCard}>
			<CardContent>
				<OffersTable
					offers={sortedOffers}
					isLoading={isLoading}
					propertyId={propertyId}
					order={order}
					orderBy={orderBy}
					onRequestSort={handleRequestSort}
				/>
			</CardContent>
		</MainCard>
	);
};

export default OfferList;
